.resume{
  font-family: "Open Sans", sans-serif;
  margin-left: 1em;
  padding-bottom: 4em;
}
h1{
  margin: 0;
}
ul{
  list-style-type: circle;
}
.contact{
  margin: 0 0 1em;
  padding: 0;
}
.contact li{
  border-left: 1px solid #1d2475;
  display: inline-block;
  font-size: 0.8em;
  padding-left: 1em;
  margin-left: 1em;
}
.contact li:first-child{
  border: none;
  margin: 0;
  padding: 0;
}
h2, h3{
  font-size: 1.25em;
}
h2{
  border-bottom: 2px solid #1d2475;
  font-weight: 200;
}
h3{
  font-weight: 50;
}
span{
  display: block;
}
.byline{
  font-size: 0.8em;
}
a{
  color: #1d2475;
}



